@import url(https://fonts.googleapis.com/css?family=Righteous);
.imageMe {
  max-width: 20%;
  height: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  padding-top: 4rem;
  padding-bottom: 2rem;
  border-radius: 8px;
}
h3 {
  font-style: normal;
  font-weight: 900;
  color: black;
  font-family: "Righteous", serif;
  padding: 1rem;
  font-size: 2em;
  text-shadow: 0.03em 0.03em 0 turquoise;
}

nav {
  opacity: 94%;
}


.icon {
  color: black;
  text-shadow: 2px 2px 5px turquoise;
}
.icon:hover {
  color: tomato;
}

ul li {
  display: inline-block;
  padding: 0 20px;
}
#contact-me {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  /*color: black;*/
  background: linear-gradient(to top, whitesmoke, lightblue);
}

h1,
h2 {
  font-style: normal;
  font-weight: 900;
  color: black;
  font-family: "Righteous", serif;
  padding: 2rem;
  font-size: 5em;
  text-shadow: 0.03em 0.03em 0 turquoise;
}
body {
  background-color: transparent;
  background: url(/react-portfolio/static/media/glassrain.d03fa7a0.jpeg) fixed no-repeat;
  background-size: cover;
  max-height: 300rem;
  border-image: linear-gradient(
    to bottom right,
    rgba(255, 255, 255, 0.75) 0%,
    rgba(245, 245, 245, 0.75) 0%,
    rgba(245, 245, 245, 0.75) 16.6%,
    rgb(245, 245, 245) 37.8%,
    rgb(245, 245, 245) 48.8%,
    rgb(254, 254, 254) 53.1%,
    rgba(245, 245, 245, 0.75) 79.4%,
    rgba(245, 245, 245, 0.75) 84.3%
  ) !important;
  border-image-slice: 1 !important;
}
.top,
.container {
  background: linear-gradient(to top, coral, lightblue);
  opacity: 94%;
  text-align: center;
}
.button {
  background-color: rgb(192, 192, 192) !important;
  color: #000 !important;
  border: none;
  display: block;
  padding: 8px 16px 5px;
  overflow: hidden;
  text-decoration: none;
  text-align: center;
  white-space: nowrap;
  border-radius: none;
  margin: 0;
  float: left;
}
a {
  /*text-decoration: none;*/
  opacity: 0.95;
  align-items: center;
  text-align: center;
  display: block;
  padding-bottom: 0.25rem;
}

a:hover {
  opacity: 1;
  color: black;
}

footer {
  /*  background-color: grey;*/
  grid-row: 10;
  grid-column: 1 / span 5;
  text-align: center;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

